import { Component, h, Element, Prop } from '@stencil/core';
import priceFormat from '../../../global/functions/price';

@Component({
  tag: 'ks-cart-select-item',
  styleUrl: 'cartSelectItem.css'
})
export class CartSelectItem {
    @Element() root: HTMLElement;

    @Prop({reflect: true}) logo: string = "";
    @Prop({reflect: true}) name: string = "";
    @Prop({reflect: true}) price: number = 0;
    @Prop() color: string = "";

    render() {

        return[
            this.logo ?
                <div class="logo" style={{backgroundColor: this.color}}>
                    <ks-img vertical center src={this.logo} target="ks-cart-select-shipping, ks-cart-select-payment"></ks-img>
                </div> : null
            ,

            <div class="name">{this.name}</div>,
            this.price ?
                <div class="price">{priceFormat(this.price)}</div>
            : null
        ];
    }
}